import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext';
import { useTheme } from '../../context/ThemeContext';
import './Header.css';

function Header() {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const { user, logout } = useAuth();
  const navigate = useNavigate();
  const { theme, toggleTheme } = useTheme();

  // --- HM LOGOUT  C CP NHT ---
  const handleLogout = () => {
    logout(); // Gi hm logout t context  xa state v localStorage
    closeMenu(); // m bo menu mobile ng li
    // iu hng v trang login sau khi  xa thng tin user
    // `replace: true` ngn ngi dng nhn "Back"  quay li trang c
    navigate('/login', { replace: true }); 
  };

  const closeMenu = () => {
    setIsMobileMenuOpen(false);
  };

  return (
    <>
      <header className="header">
        <div className="header-logo">
          <Link to="/" onClick={closeMenu}>
            DURAflex Ty Nguyn
          </Link>
        </div>

        {/* --- Menu cho my tnh --- */}
        <nav className="header-nav-desktop">
          <button onClick={toggleTheme} className="theme-toggle-button">
            {theme === 'light' ? '' : ''}
          </button>

          {user ? (
            <>
              <span>Cho, {user.name}</span>
              <button className="logout-button" onClick={handleLogout}>ng xut</button>
            </>
          ) : (
            <Link to="/login">ng nhp</Link>
          )}
        </nav>

        {/* --- Nt Hamburger cho in thoi --- */}
        <button 
          className="hamburger-menu" 
          onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
        >
          {isMobileMenuOpen ? '' : ''}
        </button>
      </header>
      
      {/* --- Menu trt ra v Lp ph cho in thoi --- */}
      <div 
        className={`mobile-nav-overlay ${isMobileMenuOpen ? 'open' : ''}`} 
        onClick={closeMenu}
      ></div>
      
      <nav className={`header-nav-mobile ${isMobileMenuOpen ? 'open' : ''}`}>
        <button onClick={toggleTheme} className="theme-toggle-button-mobile">
          Chuyn sang Giao din {theme === 'light' ? 'Ti' : 'Sng'}
        </button>

        {user ? (
          <>
            <span>Cho, {user.name} ({user.phan_loai})</span>
            <button className="logout-button" onClick={handleLogout}>ng xut</button>
          </>
        ) : (
          <Link 
            to="/login" 
            onClick={closeMenu} 
            className="login-link-mobile"
          >
            ng nhp
          </Link>
        )}
      </nav>
    </>
  );
}

export default Header;