// src/pages/ProductsPage.jsx

import React, { useState, useMemo, useEffect } from 'react';
import useFetchData from '../hooks/useFetchData';
import { useAuth } from '../context/AuthContext';
import './ProductsPage.css';
import TabSlider from '../components/products/TabSlider';
import duraflexLogo from '../assets/images/duraflex-logo.png';
// *** NG DN NG L Y ***
import { getProductImage } from '../utils/imageLoader';
// ... (phn cn li ca file gi nguyn)

const PRODUCTS_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/products.json';
const PRICES_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/prices.json';
const ADVANCED_ROLES = ['Ca Hng', 'Nh My Tn', 'ad mind'];
const SPECIAL_PRICE_TYPES = ['gi nim yt', 'Gi Thu Th', 'Gi ch nh'];

function ProductsPage() {
  const { user } = useAuth();
  
  // *** KHI PHC LI CC DNG B THIU ***
  const { data: products, isLoading: isLoadingProducts } = useFetchData(PRODUCTS_URL);
  const { data: prices, isLoading: isLoadingPrices } = useFetchData(PRICES_URL);
  
  const [selectedPriceType, setSelectedPriceType] = useState('Gi ch nh');

  const availablePriceTypes = useMemo(() => {
    if (!prices) return [];
    const priceKeys = new Set();
    const excludedKeys = ['group_id', 'id_san_pham', 'Tn sn phm', 'kch thc', 's kg trn tm', 's tm /kin', 'image sn phm'];
    prices.forEach(price => { Object.keys(price).forEach(key => { if (!excludedKeys.includes(key) && price[key]) priceKeys.add(key); }); });
    return Array.from(priceKeys);
  }, [prices]);

  useEffect(() => {
    if (!availablePriceTypes.length) return;
    if (user?.phan_loai === 'Thu Th') { setSelectedPriceType('Gi ch nh'); }
    else if (ADVANCED_ROLES.includes(user?.phan_loai)) {
      const defaultPrice = availablePriceTypes.find(p => p.toLowerCase().includes('nim yt')) || availablePriceTypes[0];
      setSelectedPriceType(defaultPrice);
    }
  }, [user, availablePriceTypes]);

  if (isLoadingProducts || isLoadingPrices || !products || !prices) {
    return <div className="page-container">Loading product details...</div>;
  }
  
  const combinedData = Object.values(products).map(product => ({
    ...product,
    variants: prices.filter(price => price.group_id === product.id),
  }));

  const shouldShowNotes = SPECIAL_PRICE_TYPES.includes(selectedPriceType);

  return (
    <>
      <div className="page-container">
        <h1>Bng Gi Sn Phm</h1>
        <p>Hin th gi theo vai tr ca bn: <strong>{user?.phan_loai}</strong></p>

        <div className="price-selector-container">
          {user?.phan_loai === 'Thu Th' ? (
            <>
              <button className={`price-selector-button ${selectedPriceType === 'Gi ch nh' ? 'active' : ''}`} onClick={() => setSelectedPriceType('Gi ch nh')}>Gi ch nh</button>
              <button className={`price-selector-button ${selectedPriceType === 'Gi Thu Th' ? 'active' : ''}`} onClick={() => setSelectedPriceType('Gi Thu Th')}>Gi Thu Th</button>
            </>
          ) : ADVANCED_ROLES.includes(user?.phan_loai) ? availablePriceTypes.map(priceType => (
            <button key={priceType} className={`price-selector-button ${selectedPriceType === priceType ? 'active' : ''}`} onClick={() => setSelectedPriceType(priceType)}>{priceType}</button>
          )) : null }
        </div>

        {combinedData.map(product => (
          product.variants.length > 0 && (
            <div key={product.id} className="product-group">
              <div className="product-group-title">
                {product.id.includes('duraflex') && <img src={duraflexLogo} alt="DURAfex Logo" className="logo" />}
                <h2>{product.name}</h2>
              </div>
              <div className="table-container">
                <table className="product-price-table">
                  <thead>
                    <tr>
                      <th style={{ width: '80px' }}>Hnh nh</th>
                      <th>Tn sn phm</th>
                      <th>Kch thc</th>
                      <th>S kg / tm</th>
                      <th>S tm / kin</th>
                      <th>Gi ({selectedPriceType})</th>
                    </tr>
                  </thead>
                  <tbody>
                    {product.variants.map(variant => {
                      const imageSrc = getProductImage(variant["image sn phm"]);
                      return (
                        <tr key={variant.id_san_pham}>
                          <td className="product-image-cell">
                            <img 
                              src={imageSrc} 
                              alt={variant["Tn sn phm"]} 
                              className="product-image" 
                              onError={(e) => { e.target.onerror = null; e.target.src="https://via.placeholder.com/60x40?text=No+Img"; }}
                            />
                          </td>
                          <td>{variant["Tn sn phm"]}</td>
                          <td>{variant["kch thc"]}</td>
                          <td>{variant["s kg trn tm"]}</td>
                          <td>{variant["s tm /kin"]}</td>
                          <td className="price-cell">
                            {variant[selectedPriceType] ? `${variant[selectedPriceType].toLocaleString('vi-VN')} VN` : ''}
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
          )
        ))}

        {shouldShowNotes && (
          <div className="notes-section">
            <h3>Lu :</h3>
            <ul>
              <li>Min ph vn chuyn vi tng n hng &gt;= 5,5 tr ; n hng di 5,5 tr ph vn chuyn cn cng thm 250 ngn/ n .</li>
              <li>n gi  c s chp thun ca cc i l phn phi ( Hong Sa Phc An , Ph Cng C M'gar, Ngc Linh C kuin,Xun Qunh Tuy c ,Trng Th tp.bmt.,nmt Bun Ma Thut ).</li>
              <li>Sn phm lun c QR chnh hng c dn trn tm.</li>
              <li> 2024 - DURAfex Vinh Tng. Mi quyn c bo lu.</li>
            </ul>
          </div>
        )}
      </div>
      <TabSlider />
    </>
  );
}

export default ProductsPage;