import { Routes, Route } from 'react-router-dom';

// Import cc component layout v route bo v
import MainLayout from '../components/layout/MainLayout';
import ProtectedRoute from './ProtectedRoute';
import AdminRoute from './AdminRoute';
import SalesRoute from './SalesRoute';

// Import tt c cc trang
import HomePage from '../pages/HomePage';
import LoginPage from '../pages/LoginPage';
import DashboardPage from '../pages/DashboardPage';
import ProductsPage from '../pages/ProductsPage';
import CustomerRevenuePage from '../pages/CustomerRevenuePage';
import ProductDetailPage from '../pages/ProductDetailPage'; // <-- THM DNG NY
import NotFoundPage from '../pages/NotFoundPage';

function AppRoutes() {
  return (
    <Routes>
      {/* Nhm 1: Cc trang s dng Layout chung */}
      <Route element={<MainLayout />}>
        {/* === CC TRANG CNG KHAI === */}
        {/* Ai cng c th truy cp */}
        <Route path="/" element={<HomePage />} />
        <Route path="/san-pham/:slug" element={<ProductDetailPage />} /> {/* <-- THM DNG NY */}

        {/* === CC TRANG CN NG NHP (MI VAI TR) === */}
        <Route element={<ProtectedRoute />}>
          <Route path="/products" element={<ProductsPage />} />
        </Route>
        
        {/* === CC TRANG CH CHO I TC BN HNG === */}
        <Route element={<SalesRoute />}>
          <Route path="/customer-revenue" element={<CustomerRevenuePage />} />
        </Route>

        {/* === CC TRANG CH CHO ADMIN === */}
        <Route element={<AdminRoute />}>
          <Route path="/dashboard" element={<DashboardPage />} />
        </Route>
      </Route>

      {/* Nhm 2: Cc trang khng c layout */}
      <Route path="/login" element={<LoginPage />} />

      {/* Nhm 3: Trang 404 (lun t cui cng) */}
      <Route path="*" element={<NotFoundPage />} />
    </Routes>
  );
}

export default AppRoutes;