import React from 'react';
import { Outlet } from 'react-router-dom';
import Header from './Header';
import { useAuth } from '../../context/AuthContext';
import AdminFab from '../common/AdminFab';

// Danh sch cc vai tr c th nhn thy nt FAB
const FAB_VIEWER_ROLES = ['Ca Hng', 'Nh My Tn', 'ad mind'];

function MainLayout() {
  const { user } = useAuth(); // Ly thng tin ngi dng

  // Kim tra xem vai tr ca ngi dng c nm trong danh sch c php xem FAB khng
  const canViewFab = user && FAB_VIEWER_ROLES.includes(user.phan_loai);

  return (
    <div>
      <Header />
      <main>
        {/* Ni dung ca cc trang con s c render  y */}
        <Outlet />
      </main>
      
      {/* Ch hin th nt FAB nu ngi dng c quyn */}
      {canViewFab && <AdminFab />}
    </div>
  );
}

export default MainLayout;