import React from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

// Danh sch cc vai tr c php truy cp
const ALLOWED_ROLES = ['Ca Hng', 'Nh My Tn'];

function SalesRoute() {
  const { user, isLoadingUsers } = useAuth();

  if (isLoadingUsers) {
    return <div>Verifying access...</div>;
  }

  // Kim tra xem ngi dng  ng nhp V vai tr ca h c trong danh sch c php khng
  if (user && ALLOWED_ROLES.includes(user.phan_loai)) {
    // Nu hp l, cho php render trang con (CustomerRevenuePage)
    return <Outlet />;
  }

  // Nu khng hp l (cha ng nhp hoc sai vai tr), iu hng v trang ch
  return <Navigate to="/" replace />;
}

export default SalesRoute;