import React, { useState } from 'react';
import './VideoSlider.css'; // <-- Import file CSS mi

function VideoSlider({ videoList }) {
  // State  lu video ang c chn. Mc nh l video u tin trong danh sch.
  const [selectedVideo, setSelectedVideo] = useState(videoList[0]);

  if (!videoList || videoList.length === 0) {
    return null;
  }

  return (
    // S dng className thay v style
    <div className="video-slider-container">
      <h2>Video Hng dn Thi cng</h2>
      
      {/* Danh sch cc icon */}
      <div className="icon-container">
        {videoList.map(video => (
          <div 
            key={video.id} 
            className="icon-wrapper"
            // Gi li style ng ny  thm hiu ng gch chn khi c chn
            style={{
              borderBottom: selectedVideo.id === video.id ? '3px solid #007bff' : '3px solid transparent'
            }} 
            onClick={() => setSelectedVideo(video)}
          >
            <img src={video.icon} alt={video.title} className="icon-image" />
            <p className="icon-title">{video.title}</p>
          </div>
        ))}
      </div>

      {/* Khung hin th video */}
      {selectedVideo && (
        <div className="video-wrapper">
          <iframe
            className="video-iframe"
            src={`https://www.youtube.com/embed/${selectedVideo.youtubeId}`}
            title={selectedVideo.title}
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </div>
      )}
    </div>
  );
}

export default VideoSlider;