import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import './LoginPage.css'; // <-- Import file CSS mi

// --- Import cc ti sn ---
import homeIcon from '../assets/icons/home-icon.png';

import houseImage from '../assets/images/login-background-house.png'; // Hoc .jpg

const TABS = ["Xem doanh s", "Xem bng gi new", "Xem biu  doanh thu", "Xem chit khu"];

function LoginPage() {
  const [email, setEmail] = useState('');
  const [activeTab, setActiveTab] = useState(TABS[0]);
  const { login } = useAuth();
  const navigate = useNavigate();

  const handleSubmit = (event) => {
    event.preventDefault(); 
    const isSuccess = login(email);
    if (isSuccess) {
      navigate('/'); 
    }
  };

  return (
    <div className="login-page">
      <div className="login-container">
        {/* --- Ct bn tri --- */}
        <div className="login-form-section">
          <nav className="login-tabs">
            {TABS.map(tab => (
              <button 
                key={tab}
                className={`login-tab-button ${activeTab === tab ? 'active' : ''}`}
                onClick={() => setActiveTab(tab)}
              >
                {tab}
              </button>
            ))}
          </nav>
          
          <div className="login-title-section">
            <Link to="/">
              <img src={homeIcon} alt="Home Icon" className="home-icon" />
            </Link>
            <h1>ng nhp</h1>
          </div>
          
          <p className="login-instructions">
            Cho mng tr li! Vui lng nhp email ca bn  tip tc.
          </p>
          
          <form className="login-form" onSubmit={handleSubmit}>
            <input
              type="email"
              placeholder="Nhp email ca bn"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              autoFocus
            />
            <button type="submit">
              ng nhp
            </button>
          </form>
        </div>

        {/* --- Ct bn phi --- */}
        <div className="login-image-section">
          <img src={houseImage} alt="Beautiful house" />
        </div>
      </div>
    </div>
  );
}

export default LoginPage;