import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import ReactGA from "react-ga4";

const RouteChangeTracker = () => {
  const location = useLocation();
  const [initialized, setInitialized] = useState(false);

  // Khi to GA4 (chy 1 ln duy nht)
  useEffect(() => {
    // Thay "G-XXXXXXXXXX" bng Measurement ID ca bn
    ReactGA.initialize("G-VK8GT2NP53"); //  sa li thiu du "
    setInitialized(true);
  }, []);

  // Gi s kin pageview mi khi URL thay i
  useEffect(() => {
    if (initialized) {
      ReactGA.send({ hitType: "pageview", page: location.pathname + location.search + location.hash });
    }
  }, [initialized, location]);

  return null; // Component ny khng render ra bt c th g trn giao din
};

export default RouteChangeTracker;