// src/utils/imageLoader.js

function importAll(r) {
  let images = {};
  r.keys().forEach((item) => { 
    images[item.replace('./', '')] = r(item); 
  });
  return images;
}

// *** NG DN NG: TM TRC TIP TRONG `images` ***
// ng dn t `src/utils` i ra (`..`), ri vo `assets/images`
const images = importAll(require.context('../assets/images', false, /\.(png|jpe?g|svg)$/));

export const getProductImage = (imageFilename) => {
  // Kim tra xem c tn file v file  c tn ti trong cc file  import khng
  if (imageFilename && images[imageFilename]) {
    return images[imageFilename];
  }
  
  // Nu khng tm thy, tr v nh placeholder
  try {
    // ng dn n placeholder cng phi ng
    return require('../assets/images/placeholder.png');
  } catch (e) {
    // Nu c placeholder cng li, tr v null
    console.error("Placeholder image not found!");
    return null;
  }
};