import React from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

function ProtectedRoute() {
  // Ly thng tin ngi dng t AuthContext
  const { user, isLoadingUsers } = useAuth();

  // Trng hp 1: D liu ngi dng ang c ti
  // Hin th mt thng bo loading  trnh b nhy trang t ngt
  if (isLoadingUsers) {
    return <div>Loading user data...</div>;
  }

  // Trng hp 2: D liu  ti xong v KHNG c ngi dng no ng nhp
  if (!user) {
    // Nu khng c user, iu hng h v trang login
    return <Navigate to="/login" replace />; 
    // `replace` gip ngi dng khng th nhn "Back"  quay li trang  b chn
  }
  
  // Trng hp 3: D liu  ti xong v C ngi dng ng nhp
  // Cho php hin th ni dung ca trang c bo v
  return <Outlet />;
  // <Outlet /> l mt placeholder c bit ca react-router-dom, 
  // n s render component con ca route (v d: HomePage)
}

export default ProtectedRoute;