import React, { createContext, useState, useContext, useEffect } from 'react';
import useFetchData from '../hooks/useFetchData';

const USERS_DATA_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/users.json';
const AuthContext = createContext(null);

const getUserFromStorage = () => {
  try {
    const storedUser = localStorage.getItem('user');
    return storedUser ? JSON.parse(storedUser) : null;
  } catch (error) {
    console.error("Failed to parse user from localStorage", error);
    localStorage.removeItem('user'); // Dn dp nu d liu b hng
    return null;
  }
};

export function AuthProvider({ children }) {
  const [currentUser, setCurrentUser] = useState(getUserFromStorage());
  const { data: allUsers, isLoading: isLoadingUsers } = useFetchData(USERS_DATA_URL);

  const login = (email) => {
    if (!allUsers) return false;
    const foundUser = allUsers.find(user => user.mail === email);
    if (foundUser) {
      setCurrentUser(foundUser); // Trigger useEffect  lu vo storage
      console.log('ng nhp thnh cng:', foundUser);
      return true;
    }
    alert("Email khng tn ti trong h thng!");
    return false;
  };

 const logout = () => {
    // Xa khi localStorage TRC
    localStorage.removeItem('user');
    // SAU  mi cp nht state
    setCurrentUser(null);
    // Quan trng: Hm logout trong context khng nn chu trch nhim iu hng.
    // Component gi n s t iu hng.
    console.log(' ng xut');
  };

  useEffect(() => {
    if (currentUser) {
      localStorage.setItem('user', JSON.stringify(currentUser));
    } else {
      localStorage.removeItem('user');
    }
  }, [currentUser]);

  const value = { user: currentUser, login, logout, isLoadingUsers };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  return useContext(AuthContext);
}