import React from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

// Danh sch cc vai tr c php truy cp
const ALLOWED_ROLES = ['ad mind'];

function AdminRoute() {
  const { user, isLoadingUsers } = useAuth();

  // Hin th loading trong khi ch ly thng tin user
  if (isLoadingUsers) {
    return <div>Verifying admin access...</div>;
  }

  // Kim tra xem ngi dng  ng nhp V c vai tr l 'ad mind' khng
  if (user && ALLOWED_ROLES.includes(user.phan_loai)) {
    // Nu hp l, cho php render trang con (DashboardPage)
    return <Outlet />;
  }

  // Nu khng hp l (cha ng nhp hoc sai vai tr), iu hng v trang ch
  return <Navigate to="/" replace />;
}

export default AdminRoute;